@import url('https://fonts.googleapis.com/css2?family=Karla&family=Source+Code+Pro&display=swap');

:root {
    margin: 0;
    font-family: "Source Code Pro", sans-serif;
}

* {
    --color-bg: rgba(29, 29, 32, .95);
    --color-1: #53CCF5;    
    --color-2: #F55F86;

    color: white;
}


body {
    background: var(--color-bg);
}

.portfolio-wrapper {
    position: absolute;
    inset: 0;
    margin: auto;
    width: 100vw;
}

.border-fill {
    position: absolute;
    width: 100%;
    height: 100%;
}
.border-fill-1 {
    background-image: linear-gradient(45deg, var(--color-1), white, var(--color-2));
    animation: 10s infinite fill-1;
}
.border-fill-2 {
    background-image: linear-gradient(45deg, var(--color-2), var(--color-1), white);
    animation: 10s infinite fill-2;
}
.border-fill-3 {
    background-image: linear-gradient(45deg, white, var(--color-2), var(--color-1));
    animation: 10s infinite fill-3;
}
.border-fill-4 {
    background-image: linear-gradient(45deg, var(--color-1), white, var(--color-2));
    animation: 10s infinite fill-4;
}

@keyframes fill-1 {
    0% {
        opacity: .7;
    }
    33.33% {
        opacity: 0;
    }
    66.66% {
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}
@keyframes fill-2 {
    0% {
        opacity: 0;
    }
    33.33% {
        opacity: .7;
    }
    66.66% {
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}
@keyframes fill-3 {
    0% {
        opacity: 0;
    }
    33.33% {
        opacity: 0;
    }
    66.66% {
        opacity: .7;
    }
    100% {
        opacity: 0;
    }
}
@keyframes fill-4 {
    0% {
        opacity: 0;
    }
    33.33% {
        opacity: 0;
    }
    66.66% {
        opacity: 0;
    }
    100% {
        opacity: .7;
    }
}

.portfolio-foreground {
    position: absolute;
    inset: 0;
    /* margin: 0 10px; */
    overflow: hidden;
    border: 30px double var(--color-bg);
}

.portfolio {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3px;
    position: absolute;
    inset: 0;
    margin: auto;
    /* border-left: 3px solid transparent;
    border-right: 3px solid transparent; */
    max-width: 1180px;
    width: 70%;
    height: 90%;
    box-shadow: 0 0 0 300px var(--color-bg);
    overflow: hidden;
}

.nav-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    flex-grow: 2;
    flex-wrap: wrap;
    padding: 10px;
    width: fit-content;
    overflow: hidden;
    box-shadow: -150px 0 0 150px var(--color-bg);
}

.nav-wrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.portfolio-icon {
    width: 100px;
    height: 100px;
    /* border-radius: 50% 50% 50% 50%; */
    cursor: pointer;
    box-shadow: 0 0 0 600px var(--color-bg);
}

.nav-button {
    font-size: 40px;
    padding: 10px;
    width: 200px;
    height: fit-content;
    cursor: pointer;
    transition: box-shadow .5s;
    text-align: center;
}

.about-button {
    /* color: var(--color-1); */
    box-shadow:
    11px -11px 2px -11px var(--color-2), -11px 11px 2px -11px var(--color-1);
}

.about-button:hover {
    box-shadow: 0 0 2px var(--color-2), 0 0 2px var(--color-1);
}

.projects-button {
    box-shadow: 0 -22px 2px -22px var(--color-1), 0 22px 2px -22px var(--color-1);

}

.projects-button:hover {
    box-shadow: 0 0 2px var(--color-1), 0 0 2px var(--color-1);
}

.contact-button {
    box-shadow: -11px -11px 2px -11px var(--color-1), 11px 11px 2px -11px var(--color-2);
}

.contact-button:hover {
    box-shadow: 0 0 2px var(--color-2), 0 0 2px var(--color-1);
}

.content-section {
    flex-basis: 600px;
    flex-grow: 3;
    width: fit-content;
    height: 300px;
    background: var(--color-bg);
    box-shadow: 200px 0 0 200px var(--color-bg);
}

.content-wrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: auto;
    max-width: 600px;
}

.content-segment {
    /* background: var(--color-bg); */
}

.content-segment > h1 {
    width: fit-content;
    font-size: 60px;
    font-weight: 500;
}

.content-segment > ul {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-auto-flow: column;
    margin-top: 10px;
    width: 250px;
}

.content-segment a {
    text-decoration: none;
    color: var(--color-2);
}

@media (max-width: 1350px) {
    .portfolio {
        flex-direction: column;
        width: 92%;
    }
    .nav-section {
        flex-grow: 0;
        flex-direction: row;
        padding-bottom: 30px;
        box-shadow: 0 -250px 0 250px var(--color-bg);
    }

    .nav-wrapper {
        flex-direction: row;
        gap: 3%;
    }

    .nav-button {
        font-size: min(40px, 4vw);
        width: min(200px, 20vw);
    }

    .portfolio-icon {
        box-shadow: 0 0 0 800px var(--color-bg);
    }

    .content-section {
        flex-basis: 350px;
        align-self: center;
        padding-top: 15px;
        width: 100%;
        box-shadow: 0 300px 0 300px var(--color-bg);
    }

    .content-segment > h1 {
        font-size: min(60px, 6vw);
    }
}